body {
  height: 100vh;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  display: flex;
}

#gameboard {
  width: 300px;
  height: 300px;
  background-color: #000;
  border: 1px solid #000;
  flex-wrap: wrap;
  display: flex;
}

.square {
  width: 100px;
  height: 100px;
  box-sizing: border-box;
  cursor: pointer;
  background-color: #fff;
  border: 2px solid #000;
  justify-content: center;
  align-items: center;
  display: flex;
}

.circle {
  height: 90px;
  width: 90px;
  box-sizing: border-box;
  border: 15px solid #00f;
  border-radius: 50%;
}

.cross {
  height: 90px;
  width: 90px;
  position: relative;
  transform: rotate(45deg);
}

.cross:before, .cross:after {
  content: "";
  background-color: red;
  position: absolute;
}

.cross:before {
  width: 30%;
  height: 100%;
  margin-left: -15%;
  left: 50%;
}

.cross:after {
  height: 30%;
  width: 100%;
  margin-top: -15%;
  top: 50%;
}

